/* You can add global styles to this file, and also import other style files */
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
    .light{
        --glass:#fff2;
    }
    .dark{
        --glass:#0002;
    }
}

@font-face {
        font-family: StrangerSans;
        src: url(/StrangerSans.ttf);
}
@font-face {
         font-family:StrangerMono;
        src: url(/StrangerMono.ttf);
}
html{
    @apply text-lg bg-slate-300 dark:bg-slate-800 text-slate-800 dark:text-gray-200 from-cyan-600 to-emerald-600 bg-gradient-to-r
}
.cc{
    @apply flex items-center justify-center
}
.glass{
    @apply bg-[var(--glass)] backdrop-blur-md shadow-md border-2 border-gray-500/20
}